import React from 'react';
import Section from "./components/Section";
import {lightGradient} from "./Helpers";
import Carousel from "./components/Carousel";

class Projects extends Section {

	constructor(props) {
		super(props);
		console.log("About Constructor", props);
		this.aboutRef = React.createRef();
	}

	componentDidMount() {
		this.props.updateRef(this.props.title, this.aboutRef);
	}

	styles = {
		...Section.styles,
		mainContainer: {
			...Section.styles.mainContainer,
			// background: "url(" + process.env.PUBLIC_URL + "/home.jpg)",
			// backgroundSize: "cover",
			backgroundImage: (this.props.backgroundColor ? lightGradient(this.props.backgroundColor) : "#fff"),
		},
		contentContainer: {
			...Section.styles.contentContainer,
			padding: "auto",
			minHeight: "100vh",
			display: "flex",
			flexDirection: "column",
			// alignContent: "center",
			justifyContent: "center",
			alignItems: "center",
			fontSize: "4.5vmin"
		},
		title: {
			...Section.styles.title,
			// fontFamily: "Yanone Kaffeesatz, sans-serif",
			fontStyle: "bold",
		},
		subTitle: {
			...Section.styles.subTitle,
			fontFamily: "Abel, sans-serif",
			maxWidth: "90%",
			color: "#ffc107",
			padding: "0.2em",
			// backgroundColor: "rgba(0,0,0,0.8)"
		},
		listItem: {
			...Section.styles.listItem,

		}
	};

	render() {
		return (
			<div style={this.styles.mainContainer} ref={this.aboutRef}>
				<div style={this.styles.contentContainer}>
					<h1 style={this.styles.title}>{this.props.title}</h1>
					{this.props.subtitle ? <h2 style={this.styles.subTitle}>{this.props.subtitle}</h2> : null}
					{/*<div style={{maxWidth:"50vw", padding:"1em"}}>*/}
					<Carousel items={this.props.data}/>
					{/*</div>*/}
				</div>
			</div>
		);
	}
}

export default Projects;