import React from 'react';
import Slider from "react-slick";

export const CarouselItem = (props) => (
	<div style={{
		display: "flex",
		flexDirection: "column",
		justifyContent: "space-between",
		alignItems: "center",
		textAlign: "center"
	}}>
		<h1>{props.title}</h1>
		<h2>{props.subTitle}</h2>
		<p>{props.description}</p>
		{
			props.data ?
				<ul style={{textAlign: "left", listStyle: "none"}}>
					{props.data.map((item, index) => <li key={index}>{item}</li>)}
				</ul>
				: null
		}
	</div>
);

class Carousel extends React.Component {
	constructor(props) {
		super(props);
		let items = props.items ? props.items : [];
		console.log("Carousel props", props.items)
		this.state = {
			items: items,
			settings: {
				dots: true,
				infinite: true,
				speed: 1000,
				autoplay: true,
				slidesToShow: 1,
				slidesToScroll: 1
			}
		}
	}

	render() {
		console.log("Carousel Items", this.state.items);
		return (
			<div style={{maxWidth: "80vw", height: "auto", padding: "10vmin"}}>
				<Slider {...this.state.settings}>
					{
						this.state.items.map((item, index) => <CarouselItem key={index} {...item}/>)
					}
				</Slider>
			</div>
		);
	}
}

export default Carousel;